import React , { Component } from 'react';

class Desc extends Component{
	constructor(props){
        super(props);
        this.state={
            width:window.innerWidth,
            height:window.innerHeight,
            
        };
        
        
     }
     
    
 
     changeSize(state){
         this.setState({width:window.innerWidth,height:window.innerHeight})
     }
 
     componentDidMount(){
         window.addEventListener('resize',() => this.changeSize(this.state));
     }
	render(){
		return(

			<div style={{marginTop:'23%',marginLeft: '10%'}}>
			<h1 style={{marginLeft:this.props.marginLeftT}}><strong>The security challenge in Industrial IoT (IIoT) and in critical<br/> infrastructure environments.</strong></h1>
			<p style={{fontSize:this.state.width/78}}>With great innovation comes great risk, it has greatly increased the industrial environment’s exposure to<br/>security threats. </p>
			<p style={{fontSize:this.state.width/78}}>Cyber-attacks pose a great risk to both business continuity and the production floor.</p>
			<p style={{fontSize:this.state.width/78}}>Businesses, governments and homes all rely on the “always on” service of their electricity networks,<br/>traffic systems, gas pipes, water facilities and other critical utility infrastructure.</p>
			<p style={{fontSize:this.state.width/78}}>The rapid growth in the sheer number of devices has greatly increased the utilities’ exposure to security <br/>threats and the risk of cyberattacks.</p>
		    <p style={{fontSize:this.state.width/78}}>smooth operations of those critical systems become mission critical to our daily lives.</p>
		</div>
			);
	};
}


export default Desc;